@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
* {
  margin: 0;
}

/*styling for all*/

 :root {
    /*
    --main-color: rgb(231, 165, 23);
    --main-color: rgb(58, 171, 216);
    */
    --main-color: black;
    --secondary-color: white;
    --third-color: #38b6ff;
    --main-font: 'Work Sans';
    --secondary-font: 'Montserrat';
    --third-font: sans-serif;
}

body,
html {
    overflow-x: hidden !important;
}

body {
    background-color: white;
    background-color: var(--secondary-color);
    font-family: 'Work Sans' !important;
    font-family: var(--main-font) !important;
    position: relative;
    width: 100%;
    box-sizing: border-box;
}

a,
a:hover,
a:active,
a:focus {
    text-decoration: none !important;
    color: inherit;
}

button:active,
button:focus {
    outline: none;
}

hr {
    padding: 0;
    margin: 0;
}


.nav-item a:hover {
    background-color: inherit !important;
    cursor: pointer;
}

.logo-section {
    font-size: 20pt;
    padding: 0px;
    margin: 0px;
}

.left-section {
    text-align: left;
    float: right;
    font-size: 16pt;
    margin: 8px;
}

nav img {
    width: 40px;
    height: 40px;
    opacity: 100% !important;
}

.navbar {
    font-family: var(--secondary-font);
    color: var(--main-color);
    background-color: var(--secondary-color);
    opacity: 1;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.363);
}
.navbar-toggle {
    border: 1px solid var(--main-color);
    padding: 10px;
}

#applyBtn {
    background-color: var(--third-color);
}

#applyBtn:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.637);
}

/* Styling for body and container*/

.container-fluid {
    margin: 0;
    padding: 0;
    padding-top: 40px;
    background-color: var(--secondary-color);
    font-size: 14pt;
    color: var(--main-color);
    /*border-radius: 20px;*/
}

#contact {
    margin-top: 50px;
    margin-bottom: 50px;
}

#contact button {
    transition: all 0.3s ease-in-out;
    background-color: var(--secondary-color);
    color: var(--main-color);
    border: 2px solid var(--main-color);
    border-radius: 20px;
    padding: 7px;
    width: 80px;
}

#contact button:hover {
    background-color: var(--main-color);
    color: var(--secondary-color);
}

.container-fluid input,
.container-fluid input:active,
.container-fluid input:focus,
.container-fluid textarea,
.container-fluid textarea:active,
.container-fluid textarea:focus,
.container-fluid select {
    min-width: 350px;
    border: 2px solid var(--main-color);
    border-radius: 20px;
    padding: 7px;
    outline: none;
    resize: None;
    margin: 5px;
}

.container-fluid input:hover,
.container-fluid textarea:hover,
.container-fluid select:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.637);
}

/*End of styling for body and container*/

/*Styling for banner*/

.banner-image-container {
    position: relative;
    width: 100%;
    margin: auto;
    /*
    border: 2px solid black;
    border-radius: 100%;
    */
}

.banner-image-container:before {
    content: "";
    display: block;
    padding-top: 43%;
}

.banner-image {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.banner-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/*end Styling for banner*/

/* styling for cause section*/

#cause {
    text-align: center;
    align-content: center;
    margin: 50px 100px;
}

.cause-image-container {
    position: relative;
    width: 30%;
    margin: 30px auto;
    background-color: #38b6ff;
    border-radius: 100%;
}

.cause-image-container:before {
    content: "";
    display: block;
    padding-top: 100%;
}

.cause-image {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.cause-image img {
    padding: 8px;
    width: 100%;
    height: 100%;
    object-fit: cover;
}


/* end styling for cause section*/

/* About section styling*/

#about {
    margin: 50px 100px;
}

.about-container {
    margin: 40px 0;
}

.about-image-container {
    position: relative;
    width: 100%;
}

.about-image-container:before {
    content: "";
    display: block;
    padding-top: 60%;
}

.about-image {
    padding: 0 70px;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.about-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}


/* About section styling end*/


/* styling for feature section*/

#features {
    text-align: center;
    align-content: center;
    margin: 50px 10%;
}

.feature-container {
    border-radius: 5px;
    padding: 20px;
    height: 380px;
    margin: 20px auto;
}

.feature-container:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.637);
}

.feature-image-container {
    position: relative;
    width: 30%;
    margin: 15px auto;
    /*
    border: 2px solid black;
    border-radius: 100%;
    */
}

.feature-image-container:before {
    content: "";
    display: block;
    padding-top: 100%;
}

.feature-image {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.feature-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.feature-container button {
    margin-top: 20px;
    transition: all 0.3s ease-in-out;
    background-color: var(--secondary-color);
    color: #7343eb;
    border-radius: 20px;
    padding: 3px 7px;
    border: none;
}

.feature-container button:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.637);
    /*
    background-color: var(--secondary-color);
    color: #7343eb;
    */
}


/* end styling for feature section*/

/* Team section styling*/

#team {
    text-align: center;
    align-content: center;
    margin: 50px;
}

.team-container {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.363);
    color: var(--main-color);
    background-color: whitesmoke;
    width: 100%;
    padding: 10px;
    margin: 20px auto;
    border-radius: 20px;
}

.team-container:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.637);
}

.team-image-container {
    position: relative;
    width: 70%;
    margin: auto;
}

.team-image-container:before {
    content: "";
    display: block;
    padding-top: 125%;
}

.team-image {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.team-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.team-text-container {
    transition: all 0.9s ease-in-out;
    transition: .3s all ease;
    padding: 10px;
    /*opacity: 0;
    display: none;
    visibility: hidden;*/
}


/*
.team-text-container:hover {
    visibility: visible;
    opacity: 1;
    display: block;
}

.team-image-container:hover+.team-text-container {
    visibility: visible;
    opacity: 1;
    display: block;
}
*/


/* Team section styling end*/


/* styling for smaller screens*/

@media screen and (min-width:992px) {
    .row.flex-override {
        display: -ms-flex;
        display: flex;
        justify-content: space-around;
    }
    .row.flex-override>.vertical-align-center {
        display: -ms-flex;
        display: flex;
    }
    .row.flex-override .vertical-align-center .valign-center {
        align-self: center;
    }
}

@media screen and (max-width:992px) {
    .about-image {
        padding: 20px;
    }
    .team-container {
        width: 80%;
    }
    #features {
        margin: 50px 10px;
    }
    .feature-container {
        padding: 8px;
    }
    .team-container {
        width: 50%;
    }
    .team-image-container {
        width: 50%;
    }
}

@media screen and (max-width:768px) {
    .feature-container {
        width: 80%;
        height: auto;
    }
}

@media screen and (max-width:600px) {
    .container-fluid input,
    .container-fluid input:active,
    .container-fluid input:focus,
    .container-fluid textarea,
    .container-fluid textarea:active,
    .container-fluid textarea:focus,
    .container-fluid select {
        min-width: 70%;
    }
    #team {
        margin: 50px 0;
    }
    #features {
        margin: 50px 20px;
    }
    #about {
        margin: 50px 0;
    }
    .team-container {
        width: 50%;
    }
    #cause {
        margin: 50px 20px;
    }
}

